@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
:root {
  --white: #f9f9f9;
  --light-gray: #b3b3b3;
  --medium-gray: #3f3f3f;
  --medium-dark-gray: #373737;
  --translucent-medium-dark-gray: #343434e6;
  --dark-gray: #181818;
  --spotify-green: #1db954;
  --red: #e34242;
}

body {
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  color: #f9f9f9;
  color: var(--white);
  background-color: #373737;
  background-color: var(--medium-dark-gray);
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: left;
  min-height: calc(100% - 20px);
  min-width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#373737),
    to(#181818));
  background: linear-gradient(
    180deg,
    #373737,
    #181818);
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(var(--medium-dark-gray)),
    to(var(--dark-gray))
  );
  background: linear-gradient(
    180deg,
    var(--medium-dark-gray),
    var(--dark-gray)
  );
  padding-bottom: 20px;
}

@media screen and (max-width: 480px) {
  #header {
    padding: 16px 24px;
  }

  #header .app-title {
    font-size: 24px;
    flex: 1 1;
  }

  #header .show-filters {
    margin-top: 4px;
  }
}

@media screen and (min-width: 481px) {
  #header {
    padding: 16px 48px;
  }

  #header .app-title {
    font-size: 32px;
  }

  #header .show-filters {
    margin-top: 10px;
    margin-left: 30px;
  }
}

#header {
  display: flex;
}

#header .app-title {
  color: #1db954;
  color: var(--spotify-green);
  font-weight: bold;
}

#header .show-filters {
  height: 25px;
  padding: 0 15px;
  background-color: transparent;
  border: 1px solid #f9f9f9;
  border: 1px solid var(--white);
  border-radius: 50vh;
  color: #f9f9f9;
  color: var(--white);
  font-size: 11px;
  font-family: 'Montserrat', sans-serif;
  outline: none;
  display: flex;
  align-items: center;
}

#header .show-filters .arrow {
  margin-top: 4px;
  margin-left: 4px;
}

#contents {
  display: flex;
  flex-direction: row;
}

body .react-loading-skeleton {
  border-radius: 0;
}

.spotify-link {
  color: #1db954;
  color: var(--spotify-green);
  cursor: pointer;
  text-decoration: none;
}

.spotify-link:hover {
  text-decoration: underline;
}

.spotify-link::after {
  display: inline-flex;
  content: '';
  width: 12px;
  height: 12px;
  background-image: url(data:image/png;base64,);
  background-size: 12px;
  margin-left: 4px;
}

.spotify-link.white {
  color: #f9f9f9;
  color: var(--white);
}

.spotify-link.white::after {
  background-image: url(data:image/png;base64,);
}

.playlist {
  display: flex;
  text-decoration: none;
  color: var(--light-gray);
  font-size: 12px;
  cursor: pointer;
}

@media screen and (max-width: 480px) {
  .playlist {
    padding: 16px 24px;
  }
}

@media screen and (min-width: 481px) {
  .playlist {
    padding: 16px 48px;
  }
}

@media screen and (max-width: 768px) {
  .playlist.playlist-small .info {
    display: none;
  }

  .playlist.playlist-small {
    padding: 20px;
  }
}

.playlist:hover,
.playlist.active:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

.playlist.active {
  background-color: rgba(255, 255, 255, 0.2);
}

.playlist .info {
  margin: 7px 0 7px 20px;
}

.playlist .info .name {
  color: var(--white);
  font-size: 16px;
  font-weight: bold;
}

.playlist .info .owner {
  color: var(--light-gray);
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .pagination .page-limit {
    display: none;
  }

  .pagination.small {
    justify-content: center;
  }

  .pagination.small .arrow {
    font-size: 20px;
  }

  .pagination.small .page-info {
    display: none;
  }
}

@media screen and (min-width: 769px) {
  .pagination .page-limit {
    display: flex;
    flex-direction: row;
  }
}

.pagination {
  margin: 8px 16px;
  text-align: end;
  display: flex;
  justify-content: flex-end;
}

.pagination .arrow {
  cursor: pointer;
  font-size: 16px;
  margin: 1px 3px;
}

.pagination .arrow.disabled {
  opacity: 0.3;
}

.pagination select,
.pagination input[type='text'] {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 13px;
}

.pagination .page-number {
  width: 14px;
  margin: 0 5px;
}

.pagination .input {
  background-color: white;
  border-radius: 50vh;
  margin: 0 5px;
}

.pagination .page-info {
  display: flex;
  flex-direction: row;
}

.pagination.small .page-limit {
  display: none;
}

@media screen and (max-width: 480px) {
  .playlist-container.active-playlist {
    display: none;
  }

  .playlist-container .title {
    font-size: 22px;
    margin: 8px 24px;
  }
}

@media screen and (min-width: 481px) {
  .playlist-container.active-playlist .title {
    display: none;
  }

  .playlist-container .title {
    font-size: 28px;
    margin: 20px 50px;
  }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .playlist-container.active-playlist .title {
    display: none;
  }
}

@media screen and (max-width: 769px) {
  .playlist-container {
    flex: 1 1;
  }

  .playlist-container.active-playlist {
    flex: unset;
  }
}

@media screen and (min-width: 769px) {
  .playlist-container {
    flex: 2 1;
  }
}

.playlist-container .title {
  font-weight: bold;
}

.playlist-container.loading {
  opacity: 0.5;
}

.filter .react-datetime-picker__button {
  padding: 0 2px;
}

.filter .react-datetime-picker__inputGroup__divider {
  white-space: nowrap;
}

.filter .react-datetime-picker__inputGroup__amPm {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

select {
  border: none;
  border-radius: 1px;
}

.filter {
  margin-bottom: 16px;
}

.filter-name {
  font-size: 12px;
  margin: 0 5px 5px 10px;
}

.filter-input {
  border: none;
  border-radius: 50vh;
  background-color: var(--white);
  padding: 1px 10px;
  width: 210px;
  margin-right: 10px;
  border: 1px solid transparent;
}

.filter-input > input[type='text'],
.filter-input > input[type='number'],
.filter-input > select,
.react-datetime-picker__wrapper {
  border: none;
  background-color: transparent;
  height: 22px;
  width: 210px;
  color: var(--medium-dark-gray);
  outline: none;
}

.filter-input.error {
  border: 1px solid var(--red);
  box-shadow: 0 0 5px var(--red);
}

.filter .error-message {
  color: var(--red);
}

@media screen and (max-width: 768px) {
  .filter-panel.active {
    display: inline-table;
  }

  .filter-panel.visible {
    height: 145px;
  }
}

@media screen and (min-width: 769px) {
  .filter-panel {
    display: inline-flex;
  }

  .filter-panel.visible {
    height: 64px;
  }

  .auto-refresh {
    padding-top: 24px;
  }
}

.filter-panel {
  padding: 0 32px;
  height: 0px;
  -webkit-transition: height 0.3s;
  transition: height 0.3s;
  visibility: hidden;
}

.filter-panel.visible {
  -webkit-transition: height 0.3s;
  transition: height 0.3s;
  visibility: visible;
}

.auto-refresh {
  margin-left: 10px;
}

.auto-refresh input[type='checkbox'] {
  margin: 0;
}

.rc-tooltip {
  position: absolute;
  z-index: 1070;
  display: block;
  visibility: visible;
  font-size: 12px;
  padding: 1px;
}

.rc-tooltip-hidden {
  display: none;
}

.rc-tooltip-placement-top .rc-tooltip-arrow {
  left: 50%;
}

.rc-tooltip-placement-top .rc-tooltip-arrow,
.rc-tooltip-placement-topLeft .rc-tooltip-arrow,
.rc-tooltip-placement-topRight .rc-tooltip-arrow {
  bottom: -5px;
  margin-left: -6px;
  border-width: 6px 6px 0;
  border-top-color: var(--translucent-medium-dark-gray);
  padding-top: 1px;
}

.rc-tooltip-arrow,
.rc-tooltip-arrow-inner {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}

.rc-tooltip-inner {
  padding: 10px;
  text-align: left;
  text-decoration: none;
  background-color: var(--translucent-medium-dark-gray);
  border-radius: 4px;
  min-height: 34px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
}

.artist-container {
  display: flex;
}

.artist-picture {
  width: 64px;
  height: 64px;
  margin-right: 10px;
}

.artist-name {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 4px;
}

.artist-followers {
  margin-bottom: 4px;
}

.artist-link.spotify-link::after {
  width: 10px;
  height: 10px;
  background-size: 10px;
}

@media screen and (max-width: 480px) {
  .track {
    padding: 8px 0;
  }
}

@media screen and (min-width: 481px) {
  .track {
    padding: 8px 16px;
  }
}

.track {
  border-bottom: 1px solid var(--medium-gray);
}

.track:hover {
  background-color: var(--medium-gray);
}

.track .artist-list {
  color: var(--light-gray);
}

.track .artist:hover {
  text-decoration: underline;
  cursor: pointer;
}

.track-container.loading {
  opacity: 0.5;
}

/* 
          width={'152px'}
