@media (min-width: 481px) {
  .track-container {
    min-width: 50%;
  }
}

.track-container {
  padding: 24px;
  background-color: rgba(255, 255, 255, 0.2);
  flex: 3 1;
}

.track-container .close-button {
  font-size: 32px;
  cursor: pointer;
}

.track-container .close-button:hover {
  opacity: 0.5;
}

.track-container .pagination {
  margin: 8px 0;
}

.playlist-info {
  display: flex;
  flex-direction: row;
}

@media screen and (max-width: 480px) {
  .playlist-info .playlist-image {
    width: 96px;
    height: 96px;
  }

  .playlist-info .playlist-details {
    margin: 8px 0 0 16px;
  }

  .playlist-details .track-count {
    font-size: 12px;
  }

  .playlist-info .playlist-details .playlist-name {
    font-size: 32px;
  }
}

@media screen and (min-width: 481px) {
  .playlist-info .playlist-image {
    width: 152px;
    height: 152px;
  }

  .playlist-info .playlist-details {
    margin: 24px 0 0 16px;
  }

  .playlist-info .playlist-details .playlist-name {
    font-size: 36px;
  }
}

.playlist-info .playlist-details {
  flex: 1 1;
}

.playlist-info .playlist-details .list-link {
  text-transform: uppercase;
  font-size: 12px;
  margin-bottom: 4px;
}

.playlist-info .playlist-details .playlist-name {
  font-weight: bold;
  margin-bottom: 8px;
}

.playlist-info .playlist-details .track-count {
  color: var(--light-gray);
}

.playlist-info .playlist-details .playlist-owner {
  color: var(--white);
  font-weight: bold;
}

.track-list {
  margin-top: 16px;
}

